<template>
    <div class="home">
        <img src="../assets/images/home.png" alt="" width="100%">
        
        <VirusInfo :hotNews="hotNews"></VirusInfo>
        <CovidNav> </CovidNav>
        <!-- 第二个模块：四个导航栏菜单的主页 -->
        <!-- <NavListHome> </NavListHome> -->
        <CovidNum :covData="covData"></CovidNum>
        <ChinaMap></ChinaMap>
        <WorldMap></WorldMap>
        <!-- 轮播图 -->
        <br>
        <Swiper></Swiper>



    </div>
    
</template>

<script>
import VirusInfo from './covidInfo/VirusInfo'
import CovidNum from './covidInfo/CovidNum'
import CovidNav from './covidInfo/CovidNav'
import NavListHome from './NumberNav/NavListHome'
import ChinaMap from './echarts/ChinaMap'
import WorldMap from './echarts/WorldMap'
import Swiper from './vueSwiper/Swiper'

import api from '../api/index'
export default{
    name: 'Home',
    components:{
        VirusInfo,
        CovidNav,
        NavListHome,
        CovidNum,
        ChinaMap, //中国疫情地图
        WorldMap, //世界疫情地图
        Swiper,
    },
    data() {
        return {            
            hotNews:[],
            covData: {}, //全国数据统计
        };
    },

    created(){
        // 请求疫情接口
        api.getVirusInfo().then(res=>{
            // console.log(res.data.newslist[0].news)
            // 疫情热点信息
            this.hotNews = res.data.newslist[0].news;
            //3. 全国疫情的数据统计信息---
            let data = res.data.newslist[0].desc;
            this.covData = {
                currentConfirmedCount: data.currentConfirmedCount,
                confirmedCount: data.confirmedCount,
                suspectedCount: data.suspectedCount,
                curedCount: data.curedCount,
                deadCount: data.deadCount,
                seriousCount: data.seriousCount,
                suspectedIncr: data.suspectedIncr,
                currentConfirmedIncr: data.currentConfirmedIncr,
                confirmedIncr: data.confirmedIncr,
                curedIncr: data.curedIncr,
                deadIncr: data.deadIncr,
                seriousIncr: data.seriousIncr,
                modifyTime: data.modifyTime,
            };
        })
    }
}

</script>


<style scoped>

</style>


