import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
//异步
const Hesuan =()=>import('../views/hesuan/Hesuan.vue')
const FreeWatch =()=>import('../views/freewatch/FreeWatch.vue')
const Travel =()=>import('../views/travel/Travel.vue')
const Prevention =()=>import('../views/prevention/Prevention.vue')
const Cities =()=>import('../views/echarts/Cities.vue')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path:'/hesuan',
      component:Hesuan
    },
    {
      path:'/freewatch',
      component:FreeWatch
    },
    {
      path:'/prevention',
      component:Prevention
    },
    {
      path:'/travel',
      component:Travel
    },
    {
      path:'/cities/:cityname',
      component:Cities,
      props:true,
    }
  ]
})



