<template>
    <div class="info">
        
        <div class="hot">
            <div class="title">
                <img src="../../assets/images/hot.png" alt="" width="70px">
                <a href="##">查看更多<img src="../../assets/images/more.png" alt="" width="10px"></a>
            </div>
            <ul>
                <li
                v-for = "news in hotNews" :key="news.id"
                >
                    <span>最新</span>
                    {{news.title}}
                    <p class="vertical-center"><img src="../../assets/images/right.png" alt="" width="10px"></p>
                </li>
            </ul>
        </div>
       


    </div>
</template>

<script>
export default{
    props:{       
        hotNews:{
            type: Array,
            default:function(){
                return {}
            },            
        },        
    }
}
</script>

<style scoped>
.info{
    background-color: white;
    padding: 0.3rem;
}
.hot{
    
    /* margin-bottom: 0.2rem; */
    /* background-color: red; */
    
    border-bottom: 0.01rem solid rgb(0,0,0,0.05); 
}
.hot .title{
    /* display: flex;
    align-items: center; */
    border-left:0.1rem solid rgb(65,105,226) ;
    padding-left: 0.1rem;
    margin-bottom: 0.1rem;
    
    /* background-color: red; */

}
.hot .title a{
    float: right;
    color:rgb(65,105,226);
    font-size: 0.3rem;
    padding-right: 0.2rem;

}

.hot li{
    line-height: 0.5rem;
    color: black;
    margin-bottom: 0.2rem;
    /* letter-spacing: 0.03rem; */
    font-size: 0.28rem;

}
.hot li .vertical-center{
    float: right;
    /* padding-right: 0.2rem; */
}

.hot li span{
    
    background-color: red;
    color:white;
    font-size: 0.2rem;
    padding: 0.02rem 0.06rem;
    border-radius: 0.06rem;
}
</style>



