/*
请求的接口的方法
*/

// 请求的axios
import axios from 'axios'
// 接口地址
import base from './base'

import md5 from 'md5-js'

// 请求方法
const api = {
    //  病毒获取（只请求，不解析，不在这里解析，而是在外面的对应页面组件里解析
    getVirusInfo(){
        return axios.get(base.host+base.virusInfo)
    },
    //国内疫情接口
    getChina(){
        return axios.get(base.baseUrl+base.chinaData)
        // return axios.get('http://localhost:8888/api/94/219')
        // 
    },
    getWorld(){
        return axios.get(base.baseUrl+base.worldData)
    },
    /**
     * 获取城市接口
     * 参数：format=json&appid=6938&city_name=城市名称&sign=md5加密字符串 
     */
     getCity(city_name) {
        //md5加密规则：sign = MD5( appid1formatjsontime1545829466密钥) 查看加密规则说明 密钥不需要键名，请直接跟上32位的密钥
        //步骤：1. 安装 npm i md5-js -S  2. 引入md5模块  3. 使用加密处理 
        let sign = md5('appid15143city_name' + city_name + 'formatjson13e8cc803385f6b07ac5409c5518fae9');
        let canshu = 'format=json&appid=15143&city_name=' + city_name + '&sign=' + sign;
        // console.log(base.baseUrl + base.city + '?' + canshu)
        return axios.get(base.baseUrl + base.city + '?' + canshu);
    },
    getSwiperBanner(){
        return axios.get(base.swiperBanner)
    }
}

export default api;



