<template>
    <div class="map">
        <div class="title">国内疫情地图</div>
        <!-- 地图容器 -->
        <!-- 为 ECharts 准备一个定义了宽高的 DOM -->
        <!-- <div id="nowMain" style="width: 7.5rem; height: 7rem"></div> -->
        <div class="content">
            <van-tabs v-model="active" animated @click="change">
                <van-tab title="现存确诊">
                  <div id="nowMain" style="width: 7rem; height: 6.3rem"></div>
                </van-tab>
                <van-tab title="累计确诊">
                  <div id="main" style="width: 7rem; height: 6.3rem"></div>
                </van-tab>
              </van-tabs>
        </div>
       
    
      </div>
    </div>
</template>

<script>
import api from '../../api/index';
export default {
    name: 'ChinaMap',

    data() {
        return {
            active: 0,
            arrConfirm:[],// 中国疫情累积数据
            arrCurConfirm:[],//中国疫情现存数据
            
        };
    },

    mounted() {

        // 获取中国疫情数据
        // api.getChina().then(res=>{
        //     // console.log(res.data.retdata)
        //     // 获取省份名称
        //     let provinces = res.data.retdata;
        //     if(!provinces){
        //         const myChart = this.$myChart.chinaMap("nowMain", []);
        //         // console.log(this.$myChart,this.$myChart.chinaMap("nowMain",[]))
        //         window.addEventListener('resize', function () {
        //             // 让我们的图表调用 resize这个方法
        //             myChart.resize();
        //         }); 
        //         return;        
        //     }
        //     let arrConfirm = []; //累计数据
        //     let arrCurConfirm = []; //现存数据
        //     for (let i = 0; i < provinces.length; i++) {
        //         let obj = {};//累计数据
        //         obj.name = provinces[i].xArea;
        //         obj.value = provinces[i].confirm; //累计确诊人数   curConfirm现在确诊人数
        //         let now = {};
        //         now.name = provinces[i].xArea;//现存数据
        //         now.value = provinces[i].curConfirm;
        //         arrConfirm.push(obj);
        //         arrCurConfirm.push(now);
        //     }
        //     this.arrConfirm = arrConfirm;
        //     this.arrCurConfirm  = arrCurConfirm;
        //     // console.log("城市的数据", arrConfirm);
