        //             //显示累计地图
        //         // this.$myChart.chinaMap("main", arr);
        //         //显示现存地图
        //         const myChart = this.$myChart.chinaMap("nowMain", this.arrCurConfirm);
        //         // console.log(this.$myChart,this.$myChart.chinaMap("nowMain",[]))
        //         window.addEventListener('resize', function () {
        //             // 让我们的图表调用 resize这个方法
        //             myChart.resize();
        //         });  
        //     })
           
        // })
        this.$nextTick(()=>{
                //显示累计地图
            // this.$myChart.chinaMap("main", arr);
            //显示现存地图
            const myChart = this.$myChart.chinaMap("nowMain", this.arrCurConfirm);
            // console.log(this.$myChart,myChart)
            // console.log(this.$myChart,this.$myChart.chinaMap("nowMain",[]))
            window.addEventListener('resize', function () {
                // 让我们的图表调用 resize这个方法
                myChart.resize();
            });  
        })
       
    
    },

    methods: {
         //vant第三方库在tab切换的时候，原理是v-if，此时nowMain渲染了，但是main还没有渲染，所以只能进行判断，再this.$mychart..
         change(title) {//name,title
            //   console.log("-----", title);
            if (title == 1) {
                this.$nextTick(() => {
                    this.$myChart.chinaMap("main", this.arrConfirm);
                });
            }
            },
    },
};
</script>

<style lang="less" scoped>
.map{
    background-color: white;
    padding-bottom: 0.3rem;
    /* padding: ; */
   
}
.map .content{
    margin-left: 0.3rem;
    margin-right: 0.3rem;
}
.title{
    border-left:0.1rem solid rgb(65,105,226) ;
    padding-left: 0.1rem;
    margin-bottom: 0.1rem;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
}
#nowMain,#main{
    background-color: rgb(247,247,247);
}
</style>



